import { mobileVhFix } from "./utils/mobile-vh-fix";
import { initAccordion } from "./modules/accordion/init-accordion";
import { initCustomSelect } from "./modules/custom-select/init-custom-select";
import { initHeader } from "./modules/header/init-header";
import { initModals } from "./modules/modal/init-modals";
import { initPhoneMask } from "./modules/init-phone-mask";
import { initPhoneValidation } from "./modules/init-phone-validation";
import { initTabs } from "./modules/tabs/init-tabs";
import { accordionMobile } from "./modules/header/accordion-mobile";
import { openSubmenuHandler } from "./modules/header/open-submenu-handler";
import { initCustomSlider } from "./modules/sliders/init-custom-slider";
import { initArrow } from "./modules/init-arrow";

window.addEventListener("DOMContentLoaded", () => {
  // Utils
  // ---------------------------------

  mobileVhFix();

  // Modules
  // ---------------------------------

  initHeader();
  initModals();
  initAccordion();
  initTabs();
  accordionMobile();
  openSubmenuHandler();
  initCustomSlider();
  initArrow();

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener("load", () => {
    initCustomSelect();
    initPhoneMask();
    initPhoneValidation();
  });
});

